import React, { Suspense } from "react";
import { isTagEmbed, isTagShop, isSocialEmbed } from "../../../../../actions/api";
import MediaIcon from "../../../Elements/MediaIcon/MediaIcon";
import Image from "../../../Elements/Media/Image";
import Video from "../../../Elements/Media/Video";
const Like = React.lazy(() => import('../../../Elements/Reactions/Like'));
const Network = React.lazy(() => import("../../../Elements/Network/Network"));
const Media = ({ itemData, wallID, ThemeID,  ownerId, wall }) => {
  const mediaIcon = itemData.hotspot ? false : true
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && wall.ThemeRule.autoPlay == 1 ? true : false
  const size = isTagEmbed ? wall.ThemeRule.aspectImageRatio ? wall.ThemeRule.aspectImageRatio : false : false

  return <div className="tb_g_p_media_wrap">
 {isSocialEmbed && wall.Personalization.interactive ? <Suspense fallback={<></>}><Like likeClass="tb_g_p_like" postId={itemData.id} wallID={wallID} UserDetail={wall.UserDetail} /></Suspense> : null}
            {isTagEmbed ? <div className="tb_g_p_social_top_">
            <Suspense fallback={<></>}><Network networkClass={'tb_g_p_social__ico'} network={itemData.network} isDefault={wall.ThemeRule.iconType === 1 ? true : false} ThemeRule={wall.ThemeRule} /></Suspense>
            </div> : null }
            {isSocialEmbed ? <MediaIcon itemData={itemData}  IconClass={`tb_g_p_media_icon${isTagEmbed ? ' tb_g_p_media_icon_te' :''}`} show={mediaIcon} isCenter={isTagEmbed}/> : null}
            {autoPlay ?
              <Video VideoClass="tb_g_p_video" data={itemData} wallID={wallID} themeID={ThemeID} ownerId={ownerId} size={1} controls={false} autoPlay={true} isCover={true} muted={true} />
              :
            <Image ImageClass="tb_g_p_image" data={itemData} wallID={wallID} themeID={ThemeID} ownerId={ownerId} size={1} hotspot={itemData.hotspot ? true : false} />
            }
          </div>
}
export default Media